import { Link } from "gatsby";
import React from "react";
import CallToAction from "../master/cta";
import blog1 from "../../assets/images/blogs/blog1.jpg";
import blog2 from "../../assets/images/blogs/blog2.jpg";
import blog3 from "../../assets/images/blogs/blog3.jpg";
import arrow from "../../assets/images/blogs/arrow.svg";
import Pagination from "react-bootstrap/Pagination";

import "./blogs.css";

const Blogs = () => {
  return (
    <div className='home-content'>
      <div className='header-title'>
        <div className='container'>
          <div className='header-title-text'>
            <h2>
              Our <span>Blogs</span>
            </h2>
            <ul>
              <li>
                <Link to='/'>Home</Link>
              </li>
              <li>/ Blog</li>
            </ul>
          </div>
        </div>
      </div>

      <div className='blogs-section'>
        <div className='container'>
          <div className='blogs-grid-section'>
            <div className='blogs-box'>
              <img src={blog1} alt='blogs' />
              <h4>Better Quality</h4>
              <p>
                It is a long established fact that a reader will be distracted by the readable
                content of a page . . . . . . .
              </p>
              <Link to='/blogDetails'>
                Read Post <img src={arrow} alt='' />
              </Link>
            </div>
            <div className='blogs-box'>
              <img src={blog2} alt='blogs' />
              <h4>Durable</h4>
              <p>
                It is a long established fact that a reader will be distracted by the readable
                content of a page . . . . . . .
              </p>
              <Link to='/blogDetails'>
                Read Post <img src={arrow} alt='' />
              </Link>
            </div>
            <div className='blogs-box'>
              <img src={blog3} alt='blogs' />
              <h4>Extremely Good</h4>
              <p>
                It is a long established fact that a reader will be distracted by the readable
                content of a page . . . . . . .
              </p>
              <Link to='/'>
                Read Post <img src={arrow} alt='' />
              </Link>
            </div>
          </div>
          <div className='blogs-pagination'>
            <Pagination>
              <Pagination.Prev />
              <Pagination.Item disabled>{1}</Pagination.Item>
              <Pagination.Item active>{2}</Pagination.Item>
              <Pagination.Item disabled>{3}</Pagination.Item>
              <Pagination.Next />
            </Pagination>
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};
export default Blogs;
